import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import TitleCard from "../../components/Cards/TitleCard"
import { openModal } from "../common/modalSlice"
import { CONFIRMATION_MODAL_CLOSE_TYPES, MODAL_BODY_TYPES } from '../../utils/globalConstantUtil'
import { ArrowDownIcon, EyeIcon, TrashIcon } from '@heroicons/react/24/outline'
import {
    fetchDownloadExcelOrderJerseyAction,
    fetchOrderJerseyAction
} from "../../app/service/action/memberAction";
import Loading from "../../components/Loading/Loading";
import { PaginationComponent } from "../../components/Pagination/PaginationComponent";
import { Button } from "@mui/material";
import SearchBar from "../../components/Input/SearchBar";

export const OrderJersey = () => {

    const dispatch = useDispatch()
    const [page, setPage] = useState(1);
    const [search, setSearch] = useState('');
    const data = useSelector(({ orderJerseyState }) => orderJerseyState.orderJersey.data)
    const isLoading = useSelector(({ orderJerseyState }) => orderJerseyState.orderJersey.isLoading)

    useEffect(() => {
        // @ts-ignore
        dispatch(fetchOrderJerseyAction(page, search)).then()
    }, [dispatch, page, search])


    const getStatus = (status) => {
        if (status === 'PENDING') {
            return <div className="badge badge-error">Pending</div>
        } else if (status === 'PROCESS') {
            return <div className="badge badge-info">Proses</div>
        } else if (status === 'DELIVERY') {
            return <div className="badge badge-warning">Pengiriman</div>
        } else if (status === "DONE") {
            return <div className="badge badge-success">Terkirim</div>
        } else {
            return null
        }
    }

    const deleteMemberOrderJersey = (index) => {
        dispatch(openModal({
            title: "Confirmation", bodyType: MODAL_BODY_TYPES.CONFIRMATION,
            extraObject: {
                message: `Apakah anda yakin akan menghapus data ini?`,
                type: CONFIRMATION_MODAL_CLOSE_TYPES.ORDER_JERSEY_DELETE,
                index
            }
        }))
    }

    const handleChange = (event, value) => {
        setPage(value);
        // @ts-ignore
        dispatch(fetchOrderJerseyAction(value, search)).then();
    };

    const handleDownloadExcel = () => {
        // @ts-ignore
        dispatch(fetchDownloadExcelOrderJerseyAction())
    }

    return (
        <>
            <TitleCard title="Registration" topMargin="mt-2" TopSideButtons={<SearchBar placeholderText={'Cari Nama Rider'}
                setSearchText={(input) => setSearch(input)} />}>
                {isLoading ? <Loading /> :
                    <div className="overflow-x-auto w-full">
                        <Button variant="contained" color="success" onClick={handleDownloadExcel}>
                            <ArrowDownIcon className="w-5" />DOWNLOAD EXCEL
                        </Button>
                        <table className="table w-full">
                            <thead>
                                <tr>
                                    <th>Tanggal</th>
                                    <th>Data Rider</th>
                                    {/*<th>Bukti Transfer</th>*/}
                                    <th>Size | Nomor</th>
                                    <th>Alamat</th>
                                    <th>Status</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    data.result ? data.result.map((value) => {
                                        return (
                                            <tr key={value.id}>
                                                <td>{value.createdAt}</td>
                                                <td>
                                                    <div className="flex items-center space-x-3">
                                                        {/*<div className="avatar">*/}
                                                        {/*    <div className="mask mask-squircle w-12 h-12">*/}
                                                        {/*        <img src={value.photoRider} alt="Avatar" onError={errorImage} />*/}
                                                        {/*    </div>*/}
                                                        {/*</div>*/}
                                                        <div>
                                                            <div className="font-bold">{value.fullname}</div>
                                                            <div className="text-sm opacity-50">{value.nickname}</div>
                                                            <div className="text-sm opacity-50">{value.categoryRace}</div>
                                                        </div>
                                                    </div>
                                                </td>
                                                {/*<td>*/}
                                                {/*    <div className="mask mask-squircle w-12 h-12">*/}
                                                {/*        <img src={value.transferImage} alt="Avatar" onError={errorImage} />*/}
                                                {/*    </div>*/}
                                                {/*</td>*/}
                                                <td>{value.sizeJersey} | {value.jerseyNumber}</td>
                                                <td>{value.address}</td>
                                                <td>{getStatus(value.jerseyStatus)}</td>
                                                <td>
                                                    <button className="btn btn-square btn-ghost"
                                                        onClick={() => deleteMemberOrderJersey(value.jerseyId)}>
                                                        <TrashIcon className="w-5" color={'red'} />
                                                    </button>
                                                    <button className="btn btn-square btn-ghost"
                                                        onClick={() => deleteMemberOrderJersey(value.id)}>
                                                        <EyeIcon className="w-5" color={'blue'} />
                                                    </button>
                                                </td>
                                            </tr>
                                        )
                                    }) : (
                                        <tr>
                                            <td colSpan={7} align={'center'}>DATA TIDAK DITEMUKAN</td>
                                        </tr>
                                    )
                                }
                            </tbody>
                        </table>
                        <PaginationComponent data={data} handleChange={handleChange} page={page} />
                    </div>
                }
            </TitleCard>
        </>
    )
}
